<template>
  <div class="shopping-list">
    <div id="addItem-wrapper" class="w-full">
      <h1>Add product</h1>
      <form @submit="addItem(name, imgUrl, enabled, category)" class="bg-white">
        <div class="flex-nowrap">
          <input id="item-name" v-model="name" placeholder="name" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="imgUrl" placeholder="imgUrl(optional)" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="category" placeholder="category" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="enabled" value="0" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
        </div>
        <button id="item-submit" type="submit" class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-3 rounded">Toevoegen</button>
      </form>
    </div>
    <div id="item-list">
      <div class="item-data" v-for="(item, idx) in products" :key="idx">
        <div class="item-data-wrapper">
          <div class="item-image"><img :src="getImgUrl(item.name, item.imgUrl)" height="30"/></div>
          <div class="item-description">{{item.name}} ({{item.category}})</div>
          <button @click="openEditForm(item)" class="item-edit bg-blue-500 hover:bg-blue-700 text-white font-bold rounded">
            Edit
          </button>
          <button @click="deleteItem(item.id)" class="item-list-modifier item-delete bg-red-500 hover:bg-red-700 text-white font-bold rounded">
            X
          </button>
        </div>
        <div class="item-edit-wrapper" v-if="editProductId == item.id">
          <input id="item-name" v-model="formName" placeholder="name" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="formImgUrl" placeholder="imgUrl(optional)" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="formCategory" placeholder="category" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="formEnabled" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <button @click="editItem(item)" class="item-save bg-blue-500 hover:bg-blue-700 text-white font-bold rounded">
            Save
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { db } from '../main'
export default {
  name: 'Products',
  data () {
    return {
      products: [],
      name: '',
      imgUrl: '',
      category: '',
      enabled: '0',
      editProductId: '',
      formName: '',
      formCategory: '',
      formEnabled: '',
      formImgUrl: ''
    }
  },
  firestore () {
    return {
      products: db.collection('product')
    }
  },
  methods: {
    addItem (name, imgUrl, enabled, category) {
      if (this.name !== '' && this.imgUrl !== '') {
        db.collection('product').add({name, imgUrl, enabled, category})
        // reset form field values
        this.name = ''
        this.imgUrl = ''
        this.enabled = '0'
        this.category = ''
      }
    },
    openEditForm (object) {
      this.editProductId = object.id
      this.formName = object.name
      this.formImgUrl = object.imgUrl
      this.formCategory = object.category
      this.formEnabled = object.enabled
    },
    editItem (object) {
      db.collection('product').doc(object.id).update({
        name: this.formName,
        imgUrl: this.formImgUrl,
        category: this.formCategory,
        enabled: this.formEnabled
      })
      this.editProductId = ''
    },
    deleteItem (id) {
      db.collection('product').doc(id).delete()
    },
    getImgUrl (localImg, extImg) {
      if (extImg === '') {
        // no more local loading
        // return require('../assets/products/' + localImg.toLowerCase() + '.jpg')
      } else if (extImg !== '') {
        return extImg
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#addItem-wrapper form .flex-nowrap {
  display:flex;
  flex-wrap:nowrap;
  padding:0 10px;
  justify-content:center;
}
#item-name {
  margin:0 0 20px;
  min-width:0;
}
#item-amount {
  width:40px;
  text-align:center;
}
#item-minus:hover,
#item-plus:hover {
  cursor:pointer;
}
#item-submit {

}
#item-list {

}
.item-data {
  width:100%;
}
.item-data-wrapper {
  display:flex;
  justify-content:flex-start;
  padding:10px;
}
.item-data:nth-child(even){
  background-color:#efefef;
}
.item-image img {
  display:block;
  height:50px;
  margin:0 10px 0 0;
}
.item-description {
font-size:16px;
line-height:2.5;
margin:0 0 0 5px;
text-transform:capitalize;
}
.item-edit {
  margin-left:auto;
  width:50px;
}
.item-delete {
  margin-left:20px!important;
}
.item-save {
  margin-left:20px;
  width:60px;
  height:34px;
}
.item-list-modifier {
  line-height:2.5;
  width:30px;
  margin:0 5px 0 0;
}
.item-list-modifier:hover {
  cursor:pointer;
}
a {
  color: #42b983;
}
</style>



