<template>
  <div class="shopping-list">
    <!-- Top button on home page -->
    <div class="container grocery-top-menu justify-space-between d-flex" v-if="quick_toggled == false && write_toggled == false">
      <v-btn tile small color="green" @click="addProductdrawer = !addProductdrawer" v-if="write_toggled == false && quick_toggled == false" large><v-icon left>mdi-lead-pencil</v-icon>Toevoegen</v-btn>
      <v-btn tile small color="primary" @click="toggler('quick')" v-if="quick_toggled == false && write_toggled == false" large><v-icon left>mdi-plus-box</v-icon>Kiezen</v-btn>
    </div>
    <!-- Add product (writing) form -->
    <v-navigation-drawer v-model="addProductdrawer" fixed temporary bottom>
      <div id="addItem-wrapper" class="w-full">
        <form @submit="addItem(name, amount)" class="bg-white">
          <div class="d-flex pt-4 pl-4 pr-4">
            <v-text-field label="Product" v-model="name" placeholder="Vul hier in"></v-text-field>
            <v-btn small color="red" tile @click="addItemChangeAmount('substract')" icon class="mt-5 ml-3 mr-3"><v-icon left large>mdi-minus-box</v-icon></v-btn>
            <v-text-field v-model="amount" placeholder="amount" readonly style="max-width:20px;"></v-text-field>
            <v-btn small color="green" tile @click="addItemChangeAmount('add')" icon class="mt-5 ml-3 mr-3"><v-icon left large>mdi-plus-box</v-icon></v-btn>
          </div>
          <div class="container justify-space-between d-flex flex-wrap pa-3">
            <v-btn tile small color="green" @click="closeAddProductForm()" type="submit" large class="mb-4"><v-icon left>mdi-content-save</v-icon>Opslaan</v-btn>
            <v-btn tile small color="black" @click="closeAddProductForm()" large outlined><v-icon left>mdi-close-circle</v-icon>Annuleren</v-btn>
          </div>
        </form>
      </div>
    </v-navigation-drawer>
    <!-- Quick add page -->
    <div id="products-wrapper" v-show="quick_toggled == true">
      <div class="container d-flex justify-space-between grocery-top-menu">
        <v-btn tile small color="primary" @click="toggler('quick')" large><v-icon left>mdi-arrow-left-circle</v-icon>Terug</v-btn>
        <v-btn tile small color="primary" icon large @click="quickjumpdrawer = !quickjumpdrawer"><v-icon left>mdi-menu</v-icon></v-btn>
      </div>

      <!-- Show all products to choose from -->
      <div id="product-list-wrapper">
        <div id="product-category-wrapper">
          <div :id="category.category" class="product-category" v-for="(category, idz) in categorydb" :key="idz">
            <v-banner single-line class="text-capitalize title" color="#00b0f0" style="color:white;"><v-icon color="white" size="24" class="mr-2">mdi-view-list</v-icon>{{category.category}}</v-banner>
            <div class="product-list">
              <div class="product-data" :class="{'active': (product.enabled == 1)}" @click="addItemQuick(product.name, '1', product)" v-for="(product, idx) in productlist" :key="idx" v-if="product.category === category.category">
                <div class="product-image"><img :src=product.imgUrl /></div>
                <div class="product-description" >{{product.name}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- Quick add Navigation drawer -->
    <v-navigation-drawer v-model="quickjumpdrawer" fixed temporary right>
      <v-list-item-group color="primary">
        <v-list-item class="text-left text-capitalize bold" @click="closeNavigator">
          <v-list-item-icon class="mr-3">
            <v-icon>mdi-close</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            Sluiten
          </v-list-item-content>
        </v-list-item>
        <v-list-item class="text-left text-capitalize bold" v-for="(category, idz) in categorydb" :key="idz" :href="'#' + category.category" @click="closeNavigator">
          <v-list-item-icon class="mr-3">
            <v-icon>mdi-backburger</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            {{category.category}}
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-navigation-drawer>
    <!-- Added products overview -->
    <div id="item-list" v-show="quick_toggled == false">
      <div class="item-data justify-start d-flex pa-2" v-for="(item, idx) in items" :key="idx">
        <v-btn small color="green" @click="increaseExistingAmount(item)" large icon><v-icon left large>mdi-plus-box</v-icon></v-btn>
        <v-btn small color="red" @click="decreaseExistingAmount(item)" large icon><v-icon left large>mdi-minus-box</v-icon></v-btn>
        <div class="item-description">{{item.amount}} <v-icon small>mdi-close</v-icon> {{item.name}}</div>
        <v-btn small color="red" @click="deleteItem(item.id, item.name)" large icon style="margin-left:auto;"><v-icon left large>mdi-delete-forever</v-icon></v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '../main'
export default {
  name: 'List',
  data () {
    return {
      items: [],
      productlist: [],
      categorydb: [],
      name: '',
      amount: '1',
      write_toggled: false,
      quick_toggled: false,
      currentOpenedCategory: '',
      quickjumpdrawer: false,
      addProductdrawer: false
    }
  },
  firestore () {
    return {
      items: db.collection('list').orderBy('name'),
      productlist: db.collection('product').orderBy('name'),
      categorydb: db.collection('categories').orderBy('category')
    }
  },
  methods: {
    addItem (name, amount) {
      console.log('firing addItem')
      if (this.name !== '') {
        var alreadyInList = false
        // check for duplicates
        for (var i = 0; i < this.items.length; i++) {
          if (this.items[i].name.localeCompare(name, undefined, { sensitivity: 'base' }) === 0) {
            console.log('This item is in the list! SKIP!')
            alreadyInList = true
          }
        }
        if (alreadyInList === false) {
          db.collection('list').add({name, amount})
          // check if this is already among the products
          console.log('You wrote: ' + name)
          this.checkProductList(name, 'add')

          this.toggler('write')
        }
        // reset form field values
        this.name = ''
        this.amount = '1'
      }
    },
    addItemChangeAmount (amount) {
      if (amount === 'add') {
        this.amount++
      } else if (amount === 'substract' && this.amount > 1) {
        this.amount--
      }
    },
    addItemQuick (name, amount, object) {
      console.log('quick! You clicked ' + name)
      var quickAdd = true
      var deleteFromList = false
      var itemId = ''
      if (this.items.length === 0) {
        console.log('List was empty, adding: ' + name)
        quickAdd = false
        db.collection('list').add({name, amount})
        // enable product on product list, used to add active class
        this.productSwitch(object, '1')
      } else {
        for (var i = 0; i < this.items.length; i++) {
          if (this.items[i].name.localeCompare(name, undefined, { sensitivity: 'base' }) === 0) {
            console.log('I found: ' + this.items[i].name)
            quickAdd = false
            deleteFromList = true
            itemId = this.items[i].id
          }
        }
      }
      if (quickAdd === true) {
        console.log('not on the list, adding!')
        db.collection('list').add({name, amount})
        // enable product on product list, used to add active class
        this.productSwitch(object, '1')
      }
      if (deleteFromList === true) {
        this.deleteItem(itemId, name)
      }
    },
    toggler (e) {
      if (e === 'write') {
        this.write_toggled = !this.write_toggled
      } else if (e === 'quick') {
        this.quick_toggled = !this.quick_toggled
      }
    },
    toggleReset () {
      this.write_toggled = false
      this.quick_toggled = false
    },
    closeAddProductForm () {
      this.addProductdrawer = false
    },
    closeNavigator () {
      this.quickjumpdrawer = false
    },
    checkProductList (itemName, actionList) {
      if (this.productlist.length !== 0) {
        var foundName = false
        var foundObject = []
        console.log('You are now in checkProductList. itemName is now: ' + itemName)
        for (var i = 0; i < this.productlist.length; i++) {
          if (this.productlist[i].name.localeCompare(itemName, undefined, { sensitivity: 'base' }) === 0) {
            console.log('checkProductList loop name is: ' + this.productlist[i].name)
            foundName = true
            foundObject = this.productlist[i]
            console.log('I found: ' + foundObject.name)
          }
        }
        if (foundName === true) {
          // update product to enable itself
          console.log('foundName is true. productlist[i] naam is: ' + foundObject.name)
          if (actionList === 'add') {
            this.productSwitch(foundObject, '1')
          } else if (actionList === 'remove') {
            this.productSwitch(foundObject, '0')
          }
        }
      }
    },
    productSwitch (object, toggle) {
      db.collection('product').doc(object.id).update({enabled: toggle})
    },
    increaseExistingAmount (object) {
      db.collection('list').doc(object.id).update({amount: Number(object.amount) + 1})
    },
    decreaseExistingAmount (object) {
      if (Number(object.amount) > 1) {
        db.collection('list').doc(object.id).update({amount: Number(object.amount) - 1})
      }
    },
    deleteItem (id, name) {
      this.checkProductList(name, 'remove')
      db.collection('list').doc(id).delete()
      console.log(id)
    },
    openCategory (name) {
      console.log('run openCategory')
      this.currentOpenedCategory = name
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.shopping-list {
  position:relative;
  padding-top:60px;
}
.grocery-top-menu {
  position:fixed;
  top:0;
  z-index:10;
  border-bottom:2px solid grey;
  padding:10px 0 10px 0;
  max-width:1256px;
  background-color:white;
}
.item-data:nth-child(even){
  background-color:#efefef;
}
.item-description {
font-size:16px;
line-height:2.5;
margin:0 0 0 5px;
text-transform:capitalize;
}
/*override */
.v-expansion-panel-content__wrap {
 display:flex !important;
 flex-wrap:wrap;
}
#products-wrapper {
  position:relative;
}
.product-list .product-data:hover,
.item-list-modifier:hover {
  cursor:pointer;
}
#product-list-wrapper {
  width:100%;
  padding:10px 10px 0;
}
.product-list {
  display:flex;
  justify-content:space-between;
  padding:10px;
  flex-wrap:wrap;
}
.product-list .product-data {
  width:30%;
  background-color:#efefef;
  padding:5px;
  border:1px solid #efefef;
  margin:0 0 20px;
}
.product-data.active {
  border-color:#006400;
  background-color:#90EE90;
}
.product-list .product-description {
  text-align:center;
}
.product-image img {
  margin:0 auto;
}
.category-title {
  width:100%;
  height:40px;
  line-height:40px;
  text-transform:capitalize;
  margin:0 0 20px;
  padding:0;
}
.category-title:hover {
  cursor:pointer;
}
.category-active {
  background-color:green !important;
}
a {
  color: #42b983;
}
</style>



