<template>
  <div class="login-wrapper">
    <div id="login-wrapper" class="w-full">
        <div class="login-fields">
          <input id="login-email" v-model="email" placeholder="E-mail" type="text" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="login-password" v-model="password" placeholder="Password" type="password" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
        </div>
        <button id="login-submit" @click="login" class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-3 rounded">Login</button>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase/app'
require('firebase/auth')
export default {
  name: 'Login',
  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    login () {
      firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
        (user) => {
          console.log('logged in!')
          this.$router.replace('list')
        },
        (err) => {
          console.log('Oopsie daisy ' + err.message)
        }
      )
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login-wrapper {
  padding:20px 10px;
}
.login-fields input {
  width:100%;
  float:left;
  clear:both;
  margin:0 0 14px;
}
#login-submit {
width:100%;
}
</style>



